import React, { useEffect, useRef } from "react";
import "../assets/css/wlcomePage.css";
import Typewriter from "typewriter-effect";
import UmbrellaBlack from "../../umbrella/assets/images/umbrellaHeaderBottomLogo.png";
import Project1Image from "../assets/images/project1Image.jpg";
import { Link } from "react-router-dom";

export const WelcomePage = () => {
  const contRef = useRef();
  useEffect(() => {
    let id = setTimeout(() => {
      contRef.current.style.visibility = "visible";
      contRef.current.style.opacity = "1";
      contRef.current.style.transitionDuration = "2s";
    }, 3800);
    return () => clearInterval(id);
  });

  return (
    <div className="welcomePage d-flex align-items-center justify-content-center flex-column">
      <div
        ref={contRef}
        className="d-flex flex-row col-4 justify-content-between"
        style={{ padding: "75px", visibility: "hidden", opacity: 0.3 }}
      >
        <Link to="/umbrella">
          <div className="d-flex flex-column">
            <img src={UmbrellaBlack} alt="Umbrella App" />
            <h2>Umbrella</h2>
          </div>
        </Link>

        <Link to="/project1">
          <div className="d-flex flex-column">
            <img src={Project1Image} alt="project1" />
            <h2>Project1</h2>
          </div>
        </Link>
      </div>
      <Typewriter
        onInit={(typewriter) => {
          typewriter.typeString("Select application!").start();
        }}
      />
    </div>
  );
};
